<template lang="html">

  <div class="pay">
<van-nav-bar
  title=""
  left-text=""
  left-arrow
  @click-left="$router.go(-1)"
/>
<van-contact-card
  :type="cardType"
  :name="currentContact.name"
  :tel="currentContact.tel"
  @click="showList = true"
/>

<van-popup v-model="showPay" position="top" style="height:100vh;">
  <van-nav-bar
    title=""
    left-text=""
    left-arrow
    @click-left="showPay=false"
  />
  <van-cell-group>
    <van-cell title=":" :value="$store.state.detail.user.rmb+' '" />
    <van-cell title=":" :value="$store.state.detail.user.point+' '"  />
</van-cell-group>
  <van-cell-group>
    <van-field v-model="pay.Money" label=":" required />
    <van-field v-model="pay.Point" label=":" required v-if="pay.Point" />
    <van-field v-model="pay.OnLine" label=":" required v-if="pay.OnLine" />
    <footer class="pay-footer" ontouchstrat="" @click="ToPay">
      <span> </span>
    </footer>
  </van-cell-group>
</van-popup>

<!--  -->
<van-popup v-model="showList" position="bottom">
  <van-address-list
    v-model="chosenAddressId"
    :list="list"
    @add="onAdd"
    @edit="onEdit"
    @select="onSelect"
  />
</van-popup>


<!--  -->
<van-popup v-model="showEdit" position="top" style="height:100vh;">
<van-nav-bar
  title=""
  left-text=""
  left-arrow
  @click-left="showEdit=false"
/>
<van-address-edit
  :area-list="areaList"
  :address-info="addressInfo"
  @delete="onDelete"
  show-set-default
  @save="onSave"
/>
</van-popup>

    <div class="pay-product">
      <ul v-if="!confirm">
        <li v-for="k in carList">
          <a>
            <img :src="k.imgPath" alt="">
            <div>
              <h2>{{k.title}}</h2>
              <h2><span style="color:#ee7150"> {{k.type.NAME}} </span></h2>
              <p>{{k.type.PRICE}} x {{k.count}} = {{k.type.PRICE * k.count}} </p>
              <p> {{k.type.HUI_DIAN * k.count }} </p>
              <p>:{{proYunFei(k.yunfei,chosenAddressId)}} </p>
            </div>
          </a>
        </li>
      </ul>

      <!--  -->
      <div class="pay-confirm" v-else>
        
      </div>
    </div>
    <h2 class="pay-allpay">: <i></i><span>{{allpay}}</span></h2>
    <h2 class="pay-allpay">: <i>{{user_price}}</i><span> :{{user_point}}</span></h2>
    
    <div style="height:15vh;"></div>
    <footer class="pay-footer" ontouchstrat="" @click="payConfirm">
      <span></span> 
    </footer>

    


  </div>
</template>

<script>
import Util from '../../../util/common'
import { Toast } from 'vant'
import Header from '@/common/_header.vue'
