            {
              this.confirm = false;
              this.$store.commit('SET_LOADING', true);
              this.$store.state.detail.user=response.data.data;
              this.user_price=response.data.data.rmb;
              this.user_point=response.data.data.point;
            }
            else{this.confirm = false;Toast((response.data && response.data.mess)?response.data.mess:'');}
           }.bind(this),true);
  },
  computed: {

    //
    carList() {

      return this.$store.state.detail.selectedList
    },

    // 
    allpay() {
      let allpay = 0;console.log(this.carList);
      if(this.$store.state.detail.selectedList)
      {
        for (let i = 0; i < this.carList.length; i++) {
          allpay=this.$Jia(allpay,this.$Cheng(this.$store.state.detail.selectedList[i].price,this.$store.state.detail.selectedList[i].count));
          //allpay += this.$store.state.detail.selectedList[i].price*this.$store.state.detail.selectedList[i].count;
          allpay=this.$Jia(allpay,this.proYunFei(this.$store.state.detail.selectedList[i].yunfei,this.chosenAddressId,this.$store.state.detail.selectedList[i].count));
          //allpay=allpay+this.proYunFei(this.$store.state.detail.selectedList[i].yunfei,this.chosenAddressId,this.$store.state.detail.selectedList[i].count);
        }
      }
      
      return allpay.toFixed(2);
    },
    currentContact() {
      const id = this.chosenAddressId;
      return id !== null ? this.list.filter(item => item.id === id)[0] : {};
    },
cardType() {
      return this.chosenContactId !== null ? 'edit' : 'add';
    }
  },
beforeCreate(){
  this.$dopost('/sysapi/address/list/', {},
  function (response) {
  if(response.data && typeof(response.data.error) != "undefined" && response.data.error===0)
  {
    for (let k = 0; k < response.data.data.length; k++)
    {
      let t=response.data.data[k];
      this.list.push({id:t['ID'],name:t['USER_NAME'],is_default:t['ISDEFAULT'],area_id:t['QU'],tel:t['USER_TEL'],address:t['ADDRESS']});//t['SHENG_NAME']+t['SHI_NAME']+t['QU_NAME']+
      if(t['ISDEFAULT']=="1")this.chosenAddressId=t['ID'];//this.list.length-1;
      //console.log(this.list);
    }

  }
  else{this.confirm = false;Toast((response.data && response.data.mess)?response.data.mess:'');}
  }.bind(this),true);
},
  methods: {
proYunFei(fei,chosenAddressId,count){
  count=parseInt(count);
  let v=fei[0];
  let area_id=this.list.filter(item => item.id === chosenAddressId)[0];
  if(area_id)
  {
    area_id=area_id['area_id'].substr(0,2)+'0000';
    if(fei[area_id] && typeof(fei[area_id]) != "undefined")
    {
      v=fei[area_id];
    }
  }
  let price=this.$Cheng(v['PRICE'],1);
  if(count > parseInt(v['PRICE_COUNT']) && this.$Cheng(v['PRICE_NEXT'],1)>0)
  {
    let chaCount=Math.ceil(this.$Chu(count-parseInt(v['PRICE_COUNT']),v['NEXT_COUNT']));
    chaCount=this.$Cheng(v['chaCount'],v['PRICE_NEXT']);
    price=this.$Jia(price,v['PRICE_NEXT']);
  }
  if(count >= parseInt(v['BAO_YOU_COUNT']))
  {
    price=0;
  }
  return price;
},
onAdd() {
  this.addressInfo={};
  this.editIndex=null;
  this.showEdit=true;
      //Toast('');
    },
onSelect(item,index){
  //console.log(item);console.log(index);
  this.chosenAddressId=item['id'];
  this.$forceUpdate();
  this.showList = false;
},
onEdit(info, index) {console.log(info);
  this.editIndex=index;
  this.addressInfo={'id':info.id,'address_detail':info.address,'name':info.name,'tel':info.tel,'area_code':info.area_id,'is_default':(parseInt(info.is_default)==1?true:false)};
  this.showEdit=true;
  //this.$forceUpdate();
  //Toast(':' + index);
},

    // 
    onSave(info) {
      console.log(info);
      
      var obj={'id':info.id||0,'address':info.address_detail,'name':info.name,'tel':info.tel,'area_id':info.area_code,'Isdefault':info.is_default?1:0};
      this.$dopost('/sysapi/address/add/', obj,
      function (res) {
      if(res.data && typeof(res.data.error) != "undefined" && res.data.error===0)
      {
        if(this.editIndex ==null)
        {
          this.list.push({id:res.data.data.id,name:info.name,tel:info.tel,area_id:info.area_code,is_default:info.is_default?1:0,address:info.address_detail});//info.province+info.city+info.county+
        }
        else
        {
          this.list[this.editIndex]['name']=info.name;
          this.list[this.editIndex]['tel']=info.tel;
          this.list[this.editIndex]['area_id']=info.area_code;
          this.list[this.editIndex]['is_default']=info.is_default?1:0;
          this.list[this.editIndex]['address']=info.address_detail;//info.province+info.city+info.county+
        }
        
        this.chosenAddressId=res.data.data.id;
        this.showList=false;
        this.showEdit=false;
      }
      else{Toast((res.data && res.data.mess)?res.data.mess:'');}
      }.bind(this),true);
      //
    },
        // 
    onDelete(info) {
      var obj={'id':info.id};
      this.$dopost('/sysapi/address/delete/', obj,
      function (res) {
      if(res.data && typeof(res.data.error) != "undefined" && res.data.error===0 && res.data.data.state=="1")
      {
        var arr=[];
        for (let k = 0; k < this.list.length; k++)
        {
          let item=this.list[k];
          if(item['id']!=info.id)arr.push(item);
        }
        this.list=arr;
        this.showEdit=false;
      }
      else{Toast((res.data && res.data.mess)?res.data.mess:'');}
      }.bind(this),true);
    },
    ToPay(val) {
      let obj= {'user_id':this.$store.state.login.username,'api_user':this.$store.state.login.username};
      obj['id']=this.orderApi.order.id;
      obj['PAY_HUI_DIAN']=this.pay.Point || 0;
      obj['PAY_MONEY']=this.pay.Money;
      obj['PAY_CHONG_MONEY']=this.pay.OnLine || 0;
      this.$dopost('/sysapi/order/pay/', obj,
      function (res) {
      if(res.data && typeof(res.data.error) != "undefined" && res.data.error===0)
      {
        if(res.data.data && res.data.data['state'])
        {
          if(res.data.data['state']=='ok')
          {
            this.showPay=false;
            Toast('');
            this.$router.push({path:'/order'});
          }
          if(res.data.data['state']=='ChongZhi')
          {
            this.showPay=false;
            Toast('');
            
            obj= {'user_id':this.$store.state.login.username,'api_user':this.$store.state.login.username};
            obj['order_id']=this.orderApi.order.id;
            this.$dopost('/pay/order/', obj,
            function (res) {
            if(res.data && typeof(res.data.error) != "undefined" && res.data.error===0)
            {
              window.location.href= '/pay/js/id/'+res.data.data.id+'';
            }
            else{Toast((res.data && res.data.mess)?res.data.mess:'');}
            }.bind(this),true);

          }
          
        }
        else
        {
          Toast('');
        }
        
      }
      else{Toast((res.data && res.data.mess)?res.data.mess:'');}
      }.bind(this),true);
    },
    payConfirm() {
      if (this.carList) { //,
      //console.log(this.chosenAddressId);
      if(this.chosenAddressId==null || parseInt(this.chosenAddressId)<=0)
      {
        Toast('');
        return;
      }
        MessageBox
          .confirm(
            `${this.allpay}`
          )
          .then(action => { //
            let obj= {'user_id':this.$store.state.login.username,'api_user':this.$store.state.login.username};
            obj['address_id']=this.chosenAddressId;

            
            for (let k = 0; k < this.carList.length; k++) {
              let tmp=this.carList[k];
              obj['data['+k+'][id]']=tmp['id'];
              obj['data['+k+'][count]']=tmp['count'];
              obj['data['+k+'][typeid]']=tmp['type']['ID'];
              //obj['data['+k+']']={'id':tmp['id'],'count':tmp['count'],'typeid':tmp['type']['ID']};
            }

            this.$dopost('/sysapi/order/add/', obj,
          function (response) {
            if(response.data && typeof(response.data.error) != "undefined" && response.data.error===0)
            {
              this.confirm = false;
              this.$store.commit('SET_LOADING', true);
              if(response.data.data && response.data.data['order_id'] && response.data.data['order'])
              {
                this.showPay=true;
                this.orderApi=response.data.data;

                this.$store.dispatch('resetCarList');
                this.$store.dispatch('resetCount'); 
                setTimeout(() => {
                  this.$store.commit('SET_LOADING', false); //loading
                  this.confirm = true; //
                }, 300)
              }
              //Toast(''+response.data.data['order_id']);
              
            }
            else{this.confirm = false;Toast((response.data && response.data.mess)?response.data.mess:'');}
           }.bind(this),true);
/** 
            this.confirm = false;
            this.$store.commit('SET_LOADING', true);
            this.$store.dispatch('resetCarList'); //unSelectedList
            this.$store.dispatch('resetCount'); //
            setTimeout(() => {
              this.$store.commit('SET_LOADING', false); //loading
              this.confirm = true; //
            }, 300)
*/
          }, function(err) {
            //
          });
      } else { //,
        alert('')
      }

    }
  }

}
</script>

<style lang="less" scoped>
@import '../../../assets/fz.less';
.van-radio__input, .van-radio__label {
    display: inline-block;
    vertical-align: middle;
}
.pay {
    width: 100%;
    background-color: #F7F7F7;

   
    .pay-product {
        background-color: #fff;
        height: 60vw;
        overflow: auto;

        li {
            a {
                display: -webkit-flex;
                display: -ms-flex;
                display: flex;
                box-sizing: border-box;
                padding: 20*10vw/75 30*10vw/75;
                color: #4D4D4D;
                .fz(font-size,30px);
                border-bottom: 1*10vw/75 solid #DEDEDE;

                > img {
                    display: block;
                    width: 2.5*10vw;
                    height: 2.5*10vw;
                }

                > div {
                    box-sizing: border-box;
                    padding-left: 50*10vw/75;
                    width: 70%;
                    h2 {
                        padding-top: 0.09*10vw;
                        overflow: hidden;
                        word-break: keep-all;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }

                    p {
                        //text-align: right;
                        .fz(font-size,24px);
                        //padding-top: 1.4*10vw;
                    }
                }
            }
        }
    }

    .pay-allpay {
        text-align: right;
        margin-top: 6vw;
        padding: 4vw 5vw;
        .fz(font-size,32px);
        color: #999999;
        background-color: #fff;
        i,
        span {
            color: @cl;
        }
    }

    .pay-footer {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        padding-bottom: 4vw;
        span {
            display: block;
            width: 85%;
            background-color: #f44;
            border-radius: 1.3vw;
            color: #fff;
            font-size: 17px;
            padding: 4vw;
            margin: 0 auto;
            text-align: center;
            &:active {
                background-color: @cl;
            }
        }

    }

    .pay-confirm {
        padding: 20px 0;
        background-color: @cl;
        text-align: center;
        color: #fff;
        line-height: 30px;
        .fz(font-size,40);
    }
}
</style>



